@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css");

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    text-decoration: none;
    box-sizing: border-box;
    list-style: none;
}

:root {
    --white: rgb(255, 255, 255);
    --black: rgb(0, 0, 0);
    --light: rgb(240, 255, 222);
    --green: rgb(190, 234, 106);
    --blue: rgb(96, 163, 186);
    --smooth-green: rgba(157, 195, 86, 0.789);
    --dark-green: rgb(48, 69, 8);

    --box-light-under-shadow: 0 0 5px var(--dark-green);
    --box-dark-under-shadow: 0 0 6px var(--green);
    --shadow-dark: rgba(48, 69, 8, 0.16);
    --shadow-light: rgba(189, 234, 106, 0.296);

    --transition: all 400ms ease;

    --theme-light: rgb(240, 255, 222);
    --theme-dark: rgb(48, 69, 8);

    --btn-box-shadow: 0px 4px 15px 3px var(--shadow-dark);
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    line-height: 1.7;
    font-family: "Roboto", sans-serif;
    transition: var(--transition);
    background: var(--theme-light);
    color: var(--theme-dark);
}

/* ================ GENARAL STYLES ============== */
.container {
    margin: 0 auto;
    padding: 10px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 500;
}

.btn {
    text-align: center;
    padding: 10px 22px;
    transition: var(--transition);
    cursor: pointer;
    text-wrap: nowrap;
    border: 1px solid var(--green);
    background: var(--dark-green);
    color: var(--green);
    font-weight: 600;
}

.btn:hover {
    background: var(--light);
}